.main {
  width: 100%;

  z-index: 1;
  scroll-snap-type: y mandatory;
}

.main__section {
  box-sizing: border-box;
}

.main__intro {
  background-image: var(--introbg);
  background-size: cover;
  background-position: top;
}

.main__about {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
  padding-bottom: 100px;
}

.main__wakesurf {
  background-image: var(--wakesurfbg);  
  background-repeat: no-repeat;
  background-size: cover;
}

.main__boatrent {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
  padding-top: 50px;

}


.main__wakeboard{
  padding-top: 50px;
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);

  
 
}


.main__slider {

  background-color: var(--color-font);
}


.main__certificats {
  padding-top: 50px;
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);

}

.main__faq {
   padding-top: 50px;
  background: linear-gradient(261.21deg, #E3E8E2 4.83%, #ECEBE7 85.85%);
}

.main__footer {
  background: linear-gradient(256.35deg, #e3e8e2 0%, #ecebe7 91.6%);
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  
  -webkit-user-drag: none; 
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
